















import {Component, Vue} from 'vue-property-decorator';
import {Auth} from "@/api/auth/Auth";
import {UserRole} from "@/api/auth/UserRole";
import Router from '@/router';
import {User} from "@/api/auth/User";

@Component({
  name: "RedirectView"
})

export default class RedirectView extends Vue {

  get user(): User | null {  return Auth.getUser(); }

  mounted() {
    if(this.user) {
      switch (this.user.role) {
        case UserRole.ADMINISTRATOR:
          Router.push({ name: "Console@index" });
          break;
        case UserRole.MARKETER:
          Router.push({ name: "Generator@start" });
          break;
        case UserRole.DESIGNER:
          Router.push({ name: "Builder@start" });
          break;
        default:
          Router.push({ name: "Auth" });
          break;
      }
    } else {
      Router.push({ name: "Auth" });
    }
  }
}
